/* ----------- 顶部轮播图 ------------ */

/* 轮播 */
.index .banner{
    margin-top: 1rem ;
    margin-bottom: -1rem;
    width:100%;
    height: 20rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    text-align: center;
}
.banner-left, .banner-center, .banner-right{
    height:18rem;
    position: relative;
}

/* 轮播左侧 */
.banner .banner-left{
    width:45%;
    overflow: hidden;
}
.banner-left img{
    height:18rem;
    width:100%;
}

/* 轮播图 */

/* 图片放置容器 */
.carousel{
    list-style: none inside;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
}

/* 单张图片放置 */
.carousel-li{
    width: 25%;
}

/* 底部小圆点 */
.carousel-id{
    list-style: inside;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    left:35%;
    bottom: .3rem;
    z-index: 1000;
}

/* 小圆点颜色 */
.carousel-id li{
    color: #fff;
    font-size: 2.5rem;
}

/* 选中的小圆点颜色 */
.carousel-id-color{
    color:#33ff00 !important;
}

/* 轮播图左右两侧按钮 */
.carousel-left{
    position: absolute;
    left: 0;
    top:40%;
    z-index: 1000;
    width:25px;
    height:60px;
    background-color: #555;
    border-radius: 0 10px 10px 0;
    background-image: url(data:image/png;base64,);
    background-position: -9px -19px;
}
.carousel-right{
    position: absolute;
    top:40%;
    right: 0 !important;
    z-index: 1000;
    width:25px;
    height:60px;
    background-color: #555;
    border-radius: 10px 0 0 10px;
    background-image: url(data:image/png;base64,);
    background-position: -57px -19px;
}
.opacity_off{
    opacity: 0;
}
.opacity_on{
    opacity: 0.6;
}

/* 轮播中侧 */
.banner .banner-center{
    width:28%;
}
.banner-center img{
    height:8.5rem;
    width:100%;
    border-radius: 5rem 0 5rem 0;
}
.banner-center-top{
    margin-bottom: 0.75rem;
}

/* 轮播右侧 */
.banner .banner-right{
    width:25%;
    background: #2a2a2a;
    text-align: start;
    color:#fff;
    border-radius: 0 10rem 0 0;
}
.banner-right .card h4{
    margin-left:.5rem;
    color:#999;
}
.banner-right .card-detail{
    margin-left:1rem;
}

