/* 点击排行前图标颜色 */
.i-icon{
    display: inline-block;
    margin:0 .5rem;
    background-color: #a0a0a0;
    width: 16px;
    height: 16px;
    vertical-align: middle;
}
.i-icon-ul li:first-child .i-icon,.i-icon-ul li:nth-child(2) .i-icon,.i-icon-ul li:nth-child(3) .i-icon{
    background-color: #ff6600 !important;
}
.body-right-bg ul{
